@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap);
.user_loging{max-width:150px;border-color:#fff;border-radius:50px;background:linear-gradient(90deg, #ffa835 36.58%, #ff891f 94.38%)}.user_loging:hover{text-decoration:none;background:none;color:#FF891F;font-weight:normal;border:solid 2px #FF891F}.header__user{flex-direction:row;justify-content:flex-start;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;position:relative}.header__user:hover .floating_user{display:block}.header__user .floating_user{display:none;position:absolute;z-index:20;top:55px;right:0;background-color:white;border-radius:8px;padding:15px;box-shadow:0px 6px 5px 2px rgba(0,0,0,0.2)}.header__user .floating_user::after{content:"";position:absolute;top:0;right:20px;width:0;height:0;border:12px solid transparent;border-bottom-color:white;border-top:0;border-left:0;margin-top:-12px}.header__user .floating_user .row{display:flex;flex-direction:row;justify-content:space-between;align-items:center;margin-bottom:8px}.header__user .floating_user hr{opacity:0.3}.header__user .floating_user h6{cursor:pointer;margin-top:8px;font-weight:normal}.header__user img{width:40px;height:40px;margin-right:12px;border:solid 2px #fff;border-radius:50%}.header__user__btn{background-color:var(--color-gray);border:none;display:block;height:40px;padding:0 20px;font-weight:bold;font-size:18px;color:var(--color-white);border-radius:8px;cursor:pointer}

header{display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:15px 100px;background-color:#7638FF;box-shadow:0px 4px 8px rgba(0,0,0,0.25);transition:0.3s}header:hover{box-shadow:0 8px 16px 0 rgba(0,0,0,0.2)}header .logo{text-align:left;font-weight:bolder;letter-spacing:0.02em;line-height:100%;color:#fff}header nav *{color:#fff;opacity:.6}header nav a+a{margin-left:20px}header .selected{border-bottom:solid 1px #fff;font-weight:bold;opacity:1}

.Modal{position:fixed;top:0;left:0;right:0;bottom:0;z-index:200;height:100vh;width:100vw;display:flex;align-items:center;justify-content:center;background-color:rgba(0,0,0,0.4);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px)}.Modal>section{position:relative;min-width:300px;max-width:500px;min-height:300px;max-height:600px;background-color:white;padding:30px;padding-bottom:20px;border-radius:20px;overflow-y:auto;overflow-x:hidden;display:flex;flex-direction:column;align-items:center;justify-content:center}.Modal>section .close{position:absolute;top:15px;right:15px;width:20px;height:20px;z-index:20;fill:#c3c3c3;cursor:pointer}.Modal>section .close *{width:100%;height:100%}

.Modal .loging{padding:20px}.loging{display:flex;justify-content:center;align-items:center;text-align:center;flex-direction:column;text-align:center}.loging h5{font-weight:800}.loging>p{margin:5px 0 16.66667px}.loging .socialMediaBtns{display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:450px}.loging .socialMediaBtns .facebook ~ p{margin-top:16.66667px}.loging .socialMediaBtns .google,.loging .socialMediaBtns .facebook{display:inline-block;width:48%}.loging .socialMediaBtns .google button,.loging .socialMediaBtns .facebook button{display:flex;justify-content:center;align-items:center;text-align:center;border:none;font-weight:300;margin:0}.loging .socialMediaBtns .google button svg,.loging .socialMediaBtns .facebook button svg{margin-right:10px}.loging .socialMediaBtns .google{margin-right:2%}.loging .socialMediaBtns .google button{background-color:white;color:black;border:solid 1px black;font-weight:700}.loging .socialMediaBtns .facebook button{background-color:#3c5b96}.loging form .buttonConter{display:flex;justify-content:center;align-items:center;text-align:center}.loging form .buttonConter button{max-width:450px;margin-top:20px;margin-bottom:10px}.loging .changer{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:0 auto;cursor:pointer}

.small{min-width:200px;max-width:300px;box-shadow:0px 4px 8px rgba(0,0,0,0.25);transition:0.3s;border-radius:8px;padding-bottom:15px}.small:hover{box-shadow:0 8px 16px 0 rgba(0,0,0,0.2)}.small>*:not(img){padding:0 15px 8px}.small img{width:100%}.small h5{margin-top:8px;font-weight:600}.small div{display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:100%}.small div span{border-color:#fff;border-radius:20px;padding:5px 15px;color:#fff;background:linear-gradient(90deg, #ffa835 36.58%, #ff891f 94.38%);display:inline-block;font-size:.79998em}.small div small{color:#c4c4c4}.small p{font-size:.97775em}.small button{max-width:unset;width:90%;border-radius:8px;text-decoration:none;background:none;color:#FF891F;font-weight:normal;border:solid 2px #FF891F;margin:10px 5% 0px;padding:8px}.small button:hover{border-color:#fff;background:linear-gradient(90deg, #ffa835 36.58%, #ff891f 94.38%);color:#fff}

.Home{padding:30px}.Home h3{margin-bottom:20px}.Home .posts{width:100%;display:flex;width:100%;flex-direction:row;justify-content:space-between;align-items:flex-start}.Home .posts>*{width:48%}

.Log{display:flex;flex-direction:row;justify-content:space-between;align-items:center;min-height:100vh}.Log aside{height:100vh;width:50%}.Log aside img{width:100%;height:100vh;object-fit:cover}.Log .loging{width:50%;height:100vh}

.PostCreator{padding:40px;padding-top:60px}.PostCreator section{display:flex;flex-direction:row;justify-content:space-between;align-items:center}.PostCreator section form{width:48%;margin-top:30px}.PostCreator section form button{width:100%;max-width:unset}.PostCreator section img{width:48%}

.buttonConter{width:100%;display:flex;flex-direction:column}.buttonConter small{color:#ff5454;padding:0 15px;text-align:center;margin:0 auto}button{width:100%;padding:8px;max-width:200px;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content;font-size:.88887em;background-color:#1b232e;border-radius:30px;border:2px solid #1b232e;color:white;font-weight:700;letter-spacing:0.1em;cursor:pointer}button:hover{background-color:#1b232e;border-color:#1b232e}button.error{background-color:#ff5454;border:none}.button--border{background-color:white;border-color:#1b232e;color:#1b232e}.button--border svg{fill:#1b232e}.button--border:hover{border-color:#26d579;background-color:white}.button--border:hover svg{fill:#26d579}button[disabled]{pointer-events:none;opacity:0.2}.inputConter{position:relative;margin-bottom:12px;width:100%;text-align:left}.inputConter span{font-size:1.06664em;color:#c4c4c4;padding:2px 10px;background-color:white;pointer-events:none;position:absolute;left:15px;top:10px;transition:0.2s;transition-timing-function:ease;transition-timing-function:cubic-bezier(0.25, 0.1, 0.25, 1);border-radius:5px}.inputConter small{color:#ff5454;padding-left:15px}.inputConter p{font-size:.88887em;padding-top:3px}select,input,textarea{font-size:1.06664em;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:45px;padding:15px;padding-left:25px;border:1px solid #c4c4c4;color:#c4c4c4;border-radius:10px;font-weight:normal;background-color:white}select:focus,input:focus,textarea:focus{border-color:#626c75;outline:none}select:focus+span,input:focus+span,textarea:focus+span{transform:scale(0.85) translateY(-24px) translateX(-10px);color:#26d579}select:valid,input:valid,textarea:valid{border-color:#626c75}select:valid+span,input:valid+span,textarea:valid+span{color:#626c75;transform:scale(0.85) translateY(-24px) translateX(-10px)}select.error,input.error,textarea.error{border:1px solid #ff5454;background:url(https://cdn0.iconfinder.com/data/icons/shift-free/32/Error-512.png) 97% 50% no-repeat;background-position-x:90%;background-size:20px}select.error+span,input.error+span,textarea.error+span{color:#ff5454}select{background:url(http://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/br_down.png) no-repeat right;-webkit-appearance:none;background-position-x:90%}input[type="date"]{background:url(https://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/calendar_2.png) 97% 50% no-repeat;background-position-x:90%}input[type="date"]::-webkit-inner-spin-button{display:none}input[type="date"]::-webkit-calendar-picker-indicator{opacity:0}input[type="range"]{position:relative;-webkit-appearance:none;width:55%}input[type="range"]:hover:after{opacity:0.5}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-runnable-track{height:5px;cursor:pointer;-webkit-transition:0.2s;transition:0.2s;background:#e5e5e5;border-radius:5px}input[type="range"]::-webkit-slider-thumb{height:15px;width:15px;border-radius:25px;cursor:pointer;-webkit-appearance:none;margin-top:-5px;-webkit-transition:all 2s ease-in;transition:all 2s ease-in}.input--number{display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.input--number__less,.input--number__plus{font-size:1.06664em;display:flex;justify-content:center;align-items:center;text-align:center;border-radius:50%;padding:0;width:20px;height:20px;letter-spacing:unset;font-weight:400}.input--number__display{font-size:1.24441em;font-weight:700;padding:0;border:none;margin:0;text-align:center;color:#1b232e;min-height:unset;width:60px}.input--number__display:hover::-webkit-inner-spin-button,.input--number__display:hover::-webkit-outer-spin-button,.input--number__display::-webkit-inner-spin-button,.input--number__display::-webkit-outer-spin-button{-webkit-appearance:none;appearance:none;margin:0}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}*{box-sizing:border-box;padding:0;margin:0;font-family:"Lato", sans-serif;font-weight:normal;color:#1b232e;scroll-behavior:smooth}#root,.App{min-height:100vh}h1{font-size:2.22217em}h2{font-size:1.99995em}h3{font-size:1.77773em}h4{font-size:1.3333em}h5{font-size:1.24441em}h6{font-size:.97775em}p{font-size:1.06664em;color:#626c75}strong{font-weight:bold;color:inherit}em{color:inherit}a{font-size:1.06664em;text-decoration:none}a:hover{opacity:0.5}a button:hover{opacity:1}[href]{cursor:pointer}svg[fill="none"]:not(.logo):not(.loader){fill:#c4c4c4}svg:not(.logo):not(.loader):hover{fill:#26d579}svg.selected{fill:#ff5454}.fullOpacity{opacity:1 !important}.backbutton{position:absolute;top:20px;left:20px}.close{position:absolute;top:15px;right:15px;width:20px !important;height:20px !important;z-index:20}.close *{width:100%;height:100%}

